import React, { useState, useEffect } from 'react';
import { useParams, useHistory } from 'react-router-dom';

import {
  CRow,
  CCol,
  CButton
} from '@coreui/react';
import CIcon from '@coreui/icons-react';
import ProductCard from '../../components/ProductCard/ProductCard';

import Spinner from '../../components/Spinner/Spinner';
import { Title } from '../../components/TextsComponents';

import { apiFetchGet } from '../../helpers/helper';

const ProductComponent = () => {
  const history = useHistory();
  const { categorie, empresa } = useParams();
  const [produtos, setProdutos] = useState([]);
  const [loading, setLoading] = useState(false);
  const [render, setRender] = useState(false);

  async function getProducts() {
    setLoading(true);
    const response = await apiFetchGet(`opcoescardapiosVisible/search?id_empresa=${empresa}&id_categoria=${categorie}`);
    setProdutos(response.data);
    setLoading(false);
    setRender(true);
  };

  useEffect(() => {
    if (categorie && empresa) getProducts();
  }, [categorie, empresa]);

  return (
    <CRow>
      <CCol xs="12" lg="12">
        <Title size="20px">Produtos</Title>
      </CCol>
      {
        loading &&
        <CCol xs="12" lg="12">
          <Spinner />
        </CCol>
      }
      <CCol xs="12" lg="12" style={{ marginBottom: '50px' }}>
        <CRow>
          {
            produtos.map((product) => (
              <CCol xs="12" lg="3" key={product.id}>
                <ProductCard product={product} id_categoria={categorie} />
              </CCol>
            ))
          }
        </CRow >
      </CCol>
      {
        produtos.length === 0 && render &&
        <CCol xs="12" lg="12" style={{ marginBottom: '20px' }}>
          <CRow style={{ textAlign: 'center', height: '100%', alignItems: 'center' }}>
            <CCol xs="12" lg="12">
              <strong>
                <text style={{ fontSize: '16px' }}>Nenhum produto encontrado </text>
                <CIcon name="cil-warning" width={26} />
              </strong>
            </CCol>
          </CRow>
        </CCol>
      }
      <CCol xs="12" lg="12" style={{
        position: 'fixed',
        bottom: '0',
        left: '0',
        backgroundColor: '#FFF',
        borderTop: '1px solid #d8dbe0',
        zIndex: '10'
      }}>
        <CButton
          onClick={() => history.goBack()}
          style={{ width: '100%', padding: '10px', margin: '10px 0' }}
          block
          color="primary"
        >
          Voltar
        </CButton>
      </CCol>
    </CRow>
  );
};

export default ProductComponent;
