import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';

import {
  CRow,
  CCol,
  CInputCheckbox,
  CTextarea,
  CFormGroup,
  CLabel
} from '@coreui/react';
import { Title, LabelBox, Text, TitleSection } from '../TextsComponents';
import ButtonsAdd from '../ButtonAddtional/Buttons';
import Alert from 'sweetalert2';

import { apiFetchGet } from '../../helpers/helper';
import { addFormAction } from '../../store/form/actions';
import { formSelector } from '../../store/form/selectors';
import { addToCartAction } from '../../store/cart/actions';

const ProductComponent = ({ id, id_categoria, produto, setOpen, edit = false, actionAdd, setActionAdd }) => {
  const history = useHistory();
  const dispatch = useDispatch();
  const { descricao, categoriaCaradpio, disponivel, venda_agendamento, tempo_balcao } = produto;

  const form = useSelector(formSelector);
  const [porcoes, setPorcoes] = useState([]);
  const [adicionais, setAdicionais] = useState([]);
  const [adicionaisEscolhidos, setAdicionaisEscolhidos] = useState(edit ? edit.adicionaisEscolhidos : []);
  const [porcao, setPorcao] = useState(edit ? edit.porcao : false);
  const [observacao, setObservacao] = useState(false);

  const { produtos_pedidos, tempo_producao } = form;

  const palavraChave = disponivel => disponivel ? 'Disponível' : 'Indisponível';

  useEffect(() => {
    if (produto) getProductInfo();
  }, [produto]);

  useEffect(() => {
    if (actionAdd) addToCart();
  }, [actionAdd]);

  async function getProductInfo() {
    const response = await apiFetchGet(`/porcoesVisible/all/single/${id}?ativo=true`);
    setPorcoes(response.data);

    const adicionais = await apiFetchGet(`opcionaisVisible/all/single/${id_categoria}?ativo=true`);
    setAdicionais(adicionais.data);
  }

  function isInArray(id) {
    return adicionaisEscolhidos.some(adicional => id === adicional.id);
  }

  function getQuantity(id) {
    const additional = adicionaisEscolhidos.find(adicional => id === adicional.id);
    if (additional)
      return additional.quantity;
  }

  function addAdditional(item) {
    if (!isInArray(item.id))
      return setAdicionaisEscolhidos([...adicionaisEscolhidos, { ...item, quantity: 1 }]);

    const filtered = adicionaisEscolhidos.map(adicional => {
      if (adicional.id !== item.id)
        return adicional;
      return {
        ...adicional,
        quantity: adicional.quantity + 1
      };
    });
    setAdicionaisEscolhidos(filtered);
  }

  function removeAdditional(item) {
    const filtered = adicionaisEscolhidos.map(adicional => {
      if (adicional.id !== item.id)
        return adicional;

      if (adicional.quantity === 1)
        return;

      return {
        ...adicional,
        quantity: adicional.quantity - 1
      };
    });

    const removed = filtered.filter(adicional => adicional);

    setAdicionaisEscolhidos(removed);
  }

  function setOrResetActive(item) {
    if (item.id === porcao.id)
      return setPorcao(false);

    setPorcao(item);
  }

  function addToCart() {
    if (!porcao && adicionaisEscolhidos.length === 0) {
      setActionAdd(false);
      return;
    }

    const adicionais = adicionaisEscolhidos;
    const index = edit.sub_id ? edit.sub_id : form.produtos_pedidos.length + 1;

    const adicionaisMapeados = adicionais.map((adicional, i) => {
      adicional.id_produto = id;
      adicional.sub_id = porcao ? index : index + i;
      adicional.avulso = !porcao;
      adicional.total = adicional.valor;
      adicional.info = 'adicionais';

      return adicional;
    })

    if (porcao) {
      porcao.quantity = 1;
      porcao.id_produto = id;
      porcao.total = porcao.valor;
      porcao.sub_id = index;
      porcao.info = 'opcoes';
      porcao.observacao = observacao;
      porcao.agendamento = venda_agendamento
    }

    const novo_tempo_producao =
      porcao && tempo_producao > porcao.tempo_balcao
        ? tempo_producao
        : tempo_balcao;

    if (edit)
      editPorcaoAdicional(index, porcao, adicionaisMapeados, novo_tempo_producao);
    else
      dispatch(addFormAction({
        ...form,
        tempo_producao: novo_tempo_producao,
        produtos_pedidos:
          porcao
            ? [...produtos_pedidos, porcao, ...adicionaisMapeados]
            : [...produtos_pedidos, ...adicionaisMapeados]
      }));

    setOpen(false);
    setActionAdd(false);
    dispatch(addToCartAction());
    setAdicionaisEscolhidos([]);
    setPorcao(false);
    setObservacao();
    questionAlert();
  };

  function questionAlert() {
    Alert.fire({
      title: 'Deseja finalizar o pedido?',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Sim',
      cancelButtonText: 'Não, continuar comprando.'
    }).then((result) => {
      if (result.isConfirmed) {
        history.push('/finishcart');
      }
    })
  }

  function editPorcaoAdicional(sub_id, porcao, adicionaisMapeados, tempo_producao) {
    const produtos_pedidos = form.produtos_pedidos.filter(produto => produto.sub_id !== sub_id);

    const finalArray = porcao
      ? [...produtos_pedidos, porcao, ...adicionaisMapeados]
      : [...produtos_pedidos, ...adicionaisMapeados];

    const sortedFinalArray = finalArray.sort((a, b) => {
      if (a.sub_id < b.sub_id)
        return -1;
      return 1;
    })

    dispatch(addFormAction({
      ...form,
      tempo_producao,
      produtos_pedidos: sortedFinalArray
    }));
  }

  return (
    <CRow>
      <CCol xs="4" lg="4">
        <Title>{categoriaCaradpio ? categoriaCaradpio.descricao : ''}</Title>
      </CCol>
      <CCol xs="4" lg="4">
        <Text>
          <LabelBox active={disponivel} text={palavraChave(disponivel)} />
        </Text>
      </CCol>
      <CCol xs="4" lg="4" style={{ marginBottom: '5px' }}>
        <Text>
          <LabelBox active={venda_agendamento} text={palavraChave(venda_agendamento)} />
        </Text>
      </CCol>
      <CCol xs="12" lg="12">
        <Text>{descricao}</Text>
      </CCol>
      <CCol xs="12" lg="12">
        <TitleSection>Porções</TitleSection>
      </CCol>
      <CCol xs="12" lg="12">
        {
          porcoes.map((item, index) => (
            <CRow
              key={index}
              onClick={() => setOrResetActive(item)}
              style={{
                marginTop: '10px',
                marginLeft: '5px'
              }}
            >
              <CCol xs="6" lg="6">
                <Title>{item.descricao}</Title>
              </CCol>
              <CCol xs="4" lg="4">
                <Text>R$ {item.valor.toFixed(2)}</Text>
              </CCol>
              <CCol xs="2" lg="2">
                <CInputCheckbox
                  checked={item.id === porcao.id}
                  style={{ zoom: '1.5' }}
                />
              </CCol>
            </CRow>
          ))
        }
      </CCol>
      {
        adicionais.length > 0 &&
        <CCol xs="12" lg="12" style={{ marginTop: '10px' }}>
          <TitleSection>Adicionais</TitleSection>
          {
            adicionais.map((adicional, index) => (
              <CRow
                key={index}
                style={{
                  marginTop: '10px',
                  marginLeft: '5px'
                }}
              >
                <CCol xs="4" lg="6">
                  <Title>{adicional.descricao}</Title>
                </CCol>
                <CCol xs="3" lg="4">
                  <Text>R$ {adicional.valor.toFixed(2)}</Text>
                </CCol>
                <CCol xs="5" lg="2">
                  <ButtonsAdd add={addAdditional} remove={removeAdditional} quantity={getQuantity} showMinus={isInArray} adicional={adicional} />
                </CCol>
              </CRow>
            ))
          }
        </CCol>
      }
      <CCol xs="12" lg="12">
        <CFormGroup>
          <CLabel htmlFor="obs">Observações</CLabel>
          <CTextarea id="obs" rows="6" onChange={e => setObservacao(e.target.value)} />
        </CFormGroup>
      </CCol>
    </CRow>
  )
};

export default ProductComponent;
