import React from 'react';
import {
    CButton
} from '@coreui/react';
import CIcon from '@coreui/icons-react';

const ButtonsComponent = ({ add, remove, adicional, quantity, showMinus }) => {

    return (
        <div
            style={{
                display: 'flex',
                justifyContent: 'left',
                gap: '15px'
            }}
        >
            {
                showMinus(adicional.id) &&
                <>
                    <CButton color="info" onClick={() => remove(adicional)}>
                        <CIcon name="cil-minus" />
                    </CButton>
                    <div style={{ display: 'flex', alignItems: 'center' }}>
                        <span style={{ fontSize: '12px' }}>{quantity(adicional.id)}</span>
                    </div>
                </>
            }
            <CButton color="info" onClick={() => add(adicional)}>
                <CIcon name="cil-plus" />
            </CButton>
        </div>
    );
};

export default ButtonsComponent;