import React, { useState } from 'react';
import {
  CButton,
  CCard,
  CCardHeader,
  CCardBody,
  CRow,
  CCol
} from '@coreui/react';
import CIcon from '@coreui/icons-react';
import Label from '../CardCategorie/LabelActive';
import ModalProduct from '../ProductModal/Modal';
import ModalSchedule from '../ScheduleProduct/Modal';

import imgBlanck from '../../assets/image/product_blanck.jpg';

const ProductCartComponent = ({ product, id_categoria }) => {
  const { id, nome_produto, descricao, categoria, disponivel, disponivel_agendamento } = product;

  const [open, setOpen] = useState(false);
  const [openSchedule, setOpenSchedule] = useState(false);

  const palavraChave = disponivel => disponivel ? 'Disponível' : 'Indisponível';

  return (
    <>
      <CCard className="custom-card-product">
        <img
          class="card-img-top"
          src={
            product.imagem ?
              `http://powerindicaassociadoapi.kinghost.net:21249/delivery${product.imagem.url}`
              : imgBlanck
          }
          alt="Card image cap"
          onClick={() => setOpen(true)}
          style={{
            maxHeight: '300px',
            maxWidth: '100%'
          }}
        />
        <CCardHeader onClick={() => setOpen(true)}>
          <CRow>
            <CCol xs="12" lg="12">
              <strong>{nome_produto}</strong>
            </CCol>
            <CCol xs="12" lg="12">
              <text>{categoria}</text>
            </CCol>
          </CRow>
        </CCardHeader>
        <CCardBody>
          <CRow
            style={{
              alignItems: 'center'
            }}
          >
            <CCol xs="12" lg="12" style={{ paddingBottom: '10px' }}>{descricao}</CCol>
            <CCol
              xs="2"
              lg="2"
              style={{
                paddingTop: '3px',
                height: '30px',
                color: 'white',
                borderRadius: '3px',
                backgroundColor: 'green'
              }}
            >
              <CIcon name="cil-watch" width={24} onClick={() => setOpenSchedule(true)} />
            </CCol>
            <CCol xs="4" lg="4" style={{ marginRight: '20px' }}>
              <Label active={disponivel} label={palavraChave(disponivel)} />
            </CCol>
            <CCol xs="4" lg="4">
              <Label active={disponivel_agendamento} label={palavraChave(disponivel_agendamento)} />
            </CCol>
          </CRow>
        </CCardBody>
      </CCard>
      <ModalSchedule {...{ id, open: openSchedule, setOpen: setOpenSchedule }} />
      <ModalProduct {...{ id, id_categoria, open, setOpen }} />
    </>
  );
};

export default ProductCartComponent;
