import React from 'react';

const LabelComponent = ({ active, label }) => {

  return (
    <>
      {active ?
        <div
        style={{
          padding: '5px 10px',
          color: 'white',
          fontSize: '14px',
          fontWeight: 'bold',
          borderRadius: '3px',
          backgroundColor: 'green',
          width: '120px'
        }}
      >{label}</div>
      :
      <div
        style={{
          padding: '5px 10px',
          color: 'white',
          fontSize: '14px',
          fontWeight: 'bold',
          borderRadius: '3px',
          backgroundColor: 'red',
          width: '120px'
        }}
      >{label}</div>
}
    </>
  );
};

export default LabelComponent;
