import React, { useState, useEffect } from 'react';
import {
  CModal,
  CModalBody,
  CModalHeader,
  CModalTitle,
  CModalFooter,
  CButton,
  CRow,
  CCol,
  CInputCheckbox
} from '@coreui/react';
import { Time } from '../TextsComponents';

import { apiFetchGet } from '../../helpers/helper';

const ModalComponent = ({ id, open, setOpen }) => {
  const [schedule, setSchedule] = useState({});
  const [loading, setLoading] = useState(true);

  const {
    segunda,
    terca,
    quarta,
    quinta,
    sexta,
    sabado,
    domingo,
    hr_fim_dom,
    hr_fim_qua,
    hr_fim_qui,
    hr_fim_sab,
    hr_fim_seg,
    hr_fim_sex,
    hr_fim_ter,
    hr_ini_dom,
    hr_ini_qua,
    hr_ini_qui,
    hr_ini_sab,
    hr_ini_seg,
    hr_ini_sex,
    hr_ini_ter
  } = schedule;

  useEffect(() => {
    if (open) getProductInfo();
  }, [open]);

  async function getProductInfo() {
    const response = await apiFetchGet(`opcoescardapiosVisible/single/${id}`);
    setSchedule(response.data.produto);
    setLoading(false);
  };

  function formatTime(time = '') {
    return time.substring(0, 5);
  };

  return (
    <CModal
      show={open}
      onClose={setOpen}
    >
      <CModalHeader closeButton>
        <CModalTitle>Horários do Produto</CModalTitle>
      </CModalHeader>
      <CModalBody>
        <CRow>
          <CCol xs="6" lg="4" style={{ marginBottom: '10px' }}>
            <div>Segunda <CInputCheckbox checked={segunda} readOnly={true} style={{ marginLeft: '0.5rem' }} /></div>
            <Time>-- {formatTime(hr_ini_seg)}</Time>
            <Time>-- {formatTime(hr_fim_seg)}</Time>
          </CCol>
          <CCol xs="6" lg="4" style={{ marginBottom: '10px' }}>
            <div>Terça <CInputCheckbox checked={terca} readOnly={true} style={{ marginLeft: '0.5rem' }} /></div>
            <Time>-- {formatTime(hr_ini_ter)}</Time>
            <Time>-- {formatTime(hr_fim_ter)}</Time>
          </CCol>
          <CCol xs="6" lg="4" style={{ marginBottom: '10px' }}>
            <div>Quarta <CInputCheckbox checked={quarta} readOnly={true} style={{ marginLeft: '0.5rem' }} /></div>
            <Time>-- {formatTime(hr_ini_qua)}</Time>
            <Time>-- {formatTime(hr_fim_qua)}</Time>
          </CCol>
          <CCol xs="6" lg="4" style={{ marginBottom: '10px' }}>
            <div>Quinta <CInputCheckbox checked={quinta} readOnly={true} style={{ marginLeft: '0.5rem' }} /></div>
            <Time>-- {formatTime(hr_ini_qui)}</Time>
            <Time>-- {formatTime(hr_fim_qui)}</Time>
          </CCol>
          <CCol xs="6" lg="4" style={{ marginBottom: '10px' }}>
            <div>Sexta <CInputCheckbox checked={sexta} readOnly={true} style={{ marginLeft: '0.5rem' }} /></div>
            <Time>-- {formatTime(hr_ini_sex)}</Time>
            <Time>-- {formatTime(hr_fim_sex)}</Time>
          </CCol>
          <CCol xs="6" lg="4" style={{ marginBottom: '10px' }}>
            <div>Sabado <CInputCheckbox checked={sabado} readOnly={true} style={{ marginLeft: '0.5rem' }} /></div>
            <Time>-- {formatTime(hr_ini_sab)}</Time>
            <Time>-- {formatTime(hr_fim_sab)}</Time>
          </CCol>
          <CCol xs="6" lg="4" style={{ marginBottom: '10px' }}>
            <div>Domingo <CInputCheckbox checked={domingo} readOnly={true} style={{ marginLeft: '0.5rem' }} /></div>
            <Time>-- {formatTime(hr_ini_dom)}</Time>
            <Time>-- {formatTime(hr_fim_dom)}</Time>
          </CCol>
        </CRow>
      </CModalBody>
      <CModalFooter>
        <CButton
          onClick={() => setOpen(false)}
          style={{ width: '100%' }}
          block
          color="danger"
        >
          Fechar
        </CButton>
      </CModalFooter>
    </CModal>
  );
};

export default ModalComponent;
