import React, { useState, useEffect } from 'react';
import {
  CModal,
  CModalBody,
  CModalTitle,
  CModalHeader,
  CModalFooter,
  CButton
} from '@coreui/react';
import Product from './Product';

import { apiFetchGet } from '../../helpers/helper';

const ModalComponent = ({ id, id_categoria, open, setOpen }) => {
  const [product, setProduct] = useState({});
  const [addCart, setAddCart] = useState();

  useEffect(() => {
    if(open) getProducts();
  }, [open]);

  async function getProducts() {
    const response = await apiFetchGet(`opcoescardapiosVisible/single/${id}`);
    setProduct(response.data.produto);
  };

  function addToCartAction() {
    setAddCart(true);
  };

  return (
    <CModal
      show={open}
      onClose={setOpen}
      className="custom-dialog"
    >
      <CModalHeader closeButton>
        <CModalTitle>{product.nome_produto}</CModalTitle>
      </CModalHeader>
      <CModalBody>
        <Product {...{ id, id_categoria, produto: product, setOpen, actionAdd: addCart, setActionAdd: setAddCart }} />
      </CModalBody>
      <CModalFooter>
        <CButton
          block
          color="danger"
          style={{
            width: '100%'
          }}
          onClick={() => setOpen(false)}
        >
          Fechar
        </CButton>
        <CButton
          block
          color="primary"
          style={{
            width: '100%'
          }}
          onClick={() => addToCartAction()}
        >
          Adicionar ao Carrinho
        </CButton>
      </CModalFooter>
    </CModal>
  );
};

export default ModalComponent;
